'use client';

import { useState, useEffect } from 'react';
import {
  Card,
  CardContent,
} from '@/components/ui/card';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Bar,
  BarChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';
import contributorData from '../../contributor-repo-timeline.json';

// Repository list with keys
const repositories = [
  { name: 'Prebid.js', key: 'prebid-js' },
  { name: 'Prebid Server', key: 'prebid-server' },
  { name: 'Prebid Server Java', key: 'prebid-server-java' },
  { name: 'Prebid Mobile iOS', key: 'prebid-mobile-ios' },
  { name: 'Prebid Mobile Android', key: 'prebid-mobile-android' },
  { name: 'Prebid Docs', key: 'prebid-github-io' },
];

interface RepoStats {
  openedPRs: number;
  mergedPRs: number;
  openedIssues: number;
  contributors: number;
}

export default function Home() {
  const [activeMetric, setActiveMetric] = useState('merged');
  const [timeRange, setTimeRange] = useState('This Year');
  const [repoStats, setRepoStats] = useState<Record<string, RepoStats>>({});

  // Process data based on time range
  useEffect(() => {
    const processData = () => {
      if (!contributorData || !contributorData.data) {
        console.error('No contributor data available!');
        return;
      }

      const stats: Record<string, RepoStats> = {};
      
      // Initialize stats for each repo
      repositories.forEach(repo => {
        stats[repo.key] = {
          openedPRs: 0,
          mergedPRs: 0,
          openedIssues: 0,
          contributors: 0
        };
      });

      const currentDate = new Date();
      const currentYear = 2025;
      const currentWeek = getWeekNumber(currentDate);
      
      // Determine time key and period
      let timeKey = 'y';
      let targetPeriod = '';
      
      if (timeRange === 'This Week') {
        timeKey = 'w';
        targetPeriod = `${currentYear}-${String(currentWeek).padStart(2, '0')}`;
      } else if (timeRange === 'Last Week') {
        timeKey = 'w';
        targetPeriod = `${currentYear}-${String(currentWeek - 1).padStart(2, '0')}`;
      } else if (timeRange === 'This Month') {
        timeKey = 'm';
        targetPeriod = `${String(currentYear).slice(-2)}-${String(currentDate.getMonth() + 1).padStart(2, '0')}`;
      } else if (timeRange === 'Last Month') {
        timeKey = 'm';
        const lastMonth = currentDate.getMonth() === 0 ? 12 : currentDate.getMonth();
        const year = currentDate.getMonth() === 0 ? currentYear - 1 : currentYear;
        targetPeriod = `${String(year).slice(-2)}-${String(lastMonth).padStart(2, '0')}`;
      } else if (timeRange === 'This Year') {
        timeKey = 'y';
        targetPeriod = '25';
      } else if (timeRange === 'Last Year') {
        timeKey = 'y';
        targetPeriod = '24';
      }

      // Track contributors per repo
      const contributorsByRepo: Record<string, Set<string>> = {};
      repositories.forEach(repo => {
        contributorsByRepo[repo.key] = new Set();
      });

      // Process contributor data
      Object.entries(contributorData.data).forEach(([contributor, repos]: [string, any]) => {
        repositories.forEach(repo => {
          const repoData = repos[repo.key];
          if (repoData && repoData[timeKey] && repoData[timeKey][targetPeriod]) {
            const metrics = repoData[timeKey][targetPeriod];
            // Index mapping: [openedPRs, mergedPRs, reviewedPRs, mergedCommits, openedIssues]
            const openedPRs = metrics[0] || 0;
            const mergedPRs = metrics[1] || 0;
            const openedIssues = metrics[4] || 0;
            
            if (openedPRs > 0 || mergedPRs > 0 || openedIssues > 0) {
              contributorsByRepo[repo.key].add(contributor);
              stats[repo.key].openedPRs += openedPRs;
              stats[repo.key].mergedPRs += mergedPRs;
              stats[repo.key].openedIssues += openedIssues;
            }
          }
        });
      });

      // Set contributor counts
      repositories.forEach(repo => {
        stats[repo.key].contributors = contributorsByRepo[repo.key].size;
      });

      setRepoStats(stats);
    };
    
    processData();
  }, [timeRange]);

  // Helper function to get week number
  function getWeekNumber(date: Date): number {
    const year = date.getFullYear();
    const week = Math.floor((date.getTime() - new Date(year, 0, 1).getTime()) / (7 * 24 * 60 * 60 * 1000));
    return week;
  }

  // Generate chart data (placeholder for now)
  const chartData = [
    { month: 'Jan', value: 186 },
    { month: 'Feb', value: 305 },
    { month: 'Mar', value: 237 },
    { month: 'Apr', value: 73 },
    { month: 'May', value: 209 },
    { month: 'Jun', value: 214 },
  ];

  const renderRepoCard = (repo: typeof repositories[0], index: number) => {
    const stats = repoStats[repo.key] || { openedPRs: 0, mergedPRs: 0, openedIssues: 0, contributors: 0 };
    
    return (
      <div key={repo.key} style={{ width: 'calc(33.333% - 0.5rem)' }}>
        <Card>
          <div className="flex flex-row items-stretch space-y-0 border-b p-0">
            <div className="flex flex-1 flex-col justify-center gap-1 py-3" style={{ paddingLeft: '1.5rem' }}>
              <h3 className="text-base font-semibold" style={{ transform: 'scale(1.5) translate(1rem, 0.5rem)', transformOrigin: 'left center' }}>
                {repo.name}
              </h3>
              <p className="text-xs text-muted-foreground" style={{ visibility: 'hidden' }}>
                Showing total activity for the last 6 months
              </p>
            </div>
            <div className="flex">
              <button
                type="button"
                data-active={activeMetric === 'opened'}
                onClick={() => setActiveMetric('opened')}
                className={`relative z-30 flex flex-col items-center justify-center border-l px-5 py-5 bg-muted/50 hover:bg-muted/70 ${
                  activeMetric === 'opened' ? 'ring-2 ring-primary/20' : ''
                }`}
              >
                <span style={{ fontSize: '12px', marginBottom: '6px' }} className="text-muted-foreground">Opened PRs</span>
                <span style={{ fontSize: '36px', fontWeight: 'bold', lineHeight: 1, color: '#10B981' }}>{stats.openedPRs}</span>
              </button>
              
              <button
                type="button"
                data-active={activeMetric === 'merged'}
                onClick={() => setActiveMetric('merged')}
                className={`relative z-30 flex flex-col items-center justify-center border-l px-5 py-5 bg-muted/50 hover:bg-muted/70 ${
                  activeMetric === 'merged' ? 'ring-2 ring-primary/20' : ''
                }`}
              >
                <span style={{ fontSize: '12px', marginBottom: '6px' }} className="text-muted-foreground">Merged PRs</span>
                <span style={{ fontSize: '36px', fontWeight: 'bold', lineHeight: 1, color: '#3B82F6' }}>{stats.mergedPRs}</span>
              </button>
              
              <button
                type="button"
                data-active={activeMetric === 'issues'}
                onClick={() => setActiveMetric('issues')}
                className={`relative z-30 flex flex-col items-center justify-center border-l px-5 py-5 bg-muted/50 hover:bg-muted/70 ${
                  activeMetric === 'issues' ? 'ring-2 ring-primary/20' : ''
                }`}
              >
                <span style={{ fontSize: '12px', marginBottom: '6px' }} className="text-muted-foreground">Opened Issues</span>
                <span style={{ fontSize: '36px', fontWeight: 'bold', lineHeight: 1, color: '#F59E0B' }}>{stats.openedIssues}</span>
              </button>
              
              <button
                type="button"
                data-active={activeMetric === 'contributors'}
                onClick={() => setActiveMetric('contributors')}
                className={`relative z-30 flex flex-col items-center justify-center border-l px-5 py-5 bg-muted/50 hover:bg-muted/70 ${
                  activeMetric === 'contributors' ? 'ring-2 ring-primary/20' : ''
                }`}
              >
                <span style={{ fontSize: '12px', marginBottom: '6px' }} className="text-muted-foreground">Contributors</span>
                <span style={{ fontSize: '36px', fontWeight: 'bold', lineHeight: 1, color: '#8B5CF6' }}>{stats.contributors}</span>
              </button>
            </div>
          </div>
          
          <CardContent className="pt-6">
            <div className="h-[300px]">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart data={chartData} margin={{ top: 0, right: 0, bottom: 0, left: 0 }}>
                  <CartesianGrid 
                    strokeDasharray="3 3" 
                    vertical={false}
                    horizontal={true}
                    strokeOpacity={0.3}
                  />
                  <XAxis 
                    dataKey="month"
                    tickLine={false}
                    axisLine={false}
                    tick={{ fontSize: 11 }}
                    stroke="#888888"
                  />
                  <YAxis 
                    tickLine={false}
                    axisLine={false}
                    tick={{ fontSize: 11 }}
                    stroke="#888888"
                  />
                  <Tooltip 
                    contentStyle={{
                      backgroundColor: 'hsl(var(--background))',
                      border: '1px solid hsl(var(--border))',
                      borderRadius: 'var(--radius)',
                    }}
                  />
                  <Bar 
                    dataKey="value" 
                    fill="#3b82f6"
                    radius={[4, 4, 0, 0]}
                  />
                </BarChart>
              </ResponsiveContainer>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  };

  return (
    <div className="w-full py-8" style={{ paddingLeft: '0.1rem', paddingRight: '0.1rem', paddingTop: '2rem' }}>
      {/* Time Range Selector */}
      <div style={{ paddingLeft: '1rem', paddingRight: '1rem', marginBottom: '2rem' }}>
        <Select value={timeRange} onValueChange={setTimeRange}>
          <SelectTrigger className="w-[200px]">
            <SelectValue placeholder="Select time range" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="This Week">This Week</SelectItem>
            <SelectItem value="Last Week">Last Week</SelectItem>
            <SelectItem value="This Month">This Month</SelectItem>
            <SelectItem value="Last Month">Last Month</SelectItem>
            <SelectItem value="This Year">This Year</SelectItem>
            <SelectItem value="Last Year">Last Year</SelectItem>
          </SelectContent>
        </Select>
      </div>
      
      {/* First Row of Charts */}
      <div className="flex flex-row justify-between" style={{ gap: '0.75rem' }}>
        {repositories.slice(0, 3).map((repo, index) => renderRepoCard(repo, index))}
      </div>

      {/* Second Row of Charts */}
      <div className="flex flex-row justify-between" style={{ gap: '0.75rem', marginTop: '4rem' }}>
        {repositories.slice(3, 6).map((repo, index) => renderRepoCard(repo, index + 3))}
      </div>
    </div>
  );
}